.contact-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  width: 61.1464968153%;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 1023px) {
  .contact-list {
    align-self: center;
    min-width: 100px;
    column-gap: 20px;
  }
}

.contact-list__item {
  width: 46.875%;
}
@media (max-width: 1023px) {
  .contact-list__item {
    width: auto;
  }
}

.contact-list__item a {
  color: #f2f5f6;
}

.contact-list__item--mail a,
.contact-list__item--phone a {
  padding-left: 15.5555555556%;
  position: relative;
  font-size: 16px;
}
@media (max-width: 1920px) {
  .contact-list__item--mail a,
.contact-list__item--phone a {
    font-size: 0.83vw;
  }
}
@media (max-width: 1023px) {
  .contact-list__item--mail a,
.contact-list__item--phone a {
    display: block;
    min-width: 40px;
    min-height: 40px;
  }
}

.contact-list__item--mail a::after,
.contact-list__item--phone a::after {
  position: absolute;
  content: "";
  top: -1px;
  left: 0;
  background-size: cover;
  background-position: center;
}
@media (max-width: 1023px) {
  .contact-list__item--mail a::after,
.contact-list__item--phone a::after {
    background-size: contain;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.contact-list__item--mail a::after {
  background-image: url("../img/svg/icon-mail.svg");
  width: 16.0808637721%;
  height: 111.1111111111%;
}
@media (max-width: 1023px) {
  .contact-list__item--mail a::after {
    width: 21px;
    height: 20px;
  }
}

.contact-list__item--phone a::after {
  background-image: url("../img/svg/icon-phone.svg");
  width: 10.1449275362%;
  height: 111.1111111111%;
}
@media (max-width: 1023px) {
  .contact-list__item--phone a::after {
    width: 21px;
    height: 20px;
  }
}

@media (max-width: 1023px) {
  .contact-list__item--mail span,
.contact-list__item--phone span {
    display: none;
  }
}

